.btn_main {
  background: #04a3e9;
  border-radius: 6px;
  height: 50px;
  max-width: 264px;
  display: inline-block;
  padding-left: 15px;
  padding-right: 15px;
  line-height: 50px;
  
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  color: #ffffff;
  cursor: pointer;
  &:hover {
    opacity: 1;
    background-color: #2bbeff;
    color: #fff;
  }
  &:focus {
    opacity: 1;
    background-color: #0896d5;
  }

  &.disabled,
  &:disabled {
    opacity: 1;

    background-color: rgba(4, 163, 233, 0.2);
    span {
      color: #04a3e9;
      opacity: 0.5;
    }
    .icon--blue {
      display: initial;
    }
    .icon--white {
      display: none;
    }
  }
}
