}
.breadcrumbs__items {
  ul {
    @include clear;
    li {
      position: relative;
      padding-right: 7px;
      margin-right: 7px;
      display: inline-block;
      vertical-align: middle;
      &:after {
        content: "/";
        position: absolute;
        right: -6px;
        
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 16px;
        color: rgba(37, 52, 79, 0.5);
      }
      &:last-child:after {
        display: none;
      }
    }
    img {
      line-height: 1;
      vertical-align: bottom;
    }
    span {
      
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 16px;
      color: #25344f;
    }
